import Vue from 'vue';
import Router from 'vue-router';
import About from './views/About.vue';
import Blast from './views/Blast.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Blast
    },
    {
      path: '/about',
      name: 'about',
      component: About
    }
  ]
});
